.popup-share {
    display: flex;
    a {
        display: flex;
        justify-content: center;
        align-items: center;
        width: sz(32);
        height: sz(32);
        margin: 0 sz(6);
        border-radius: 50%;
        font-size: sz(16);
        color: #fff;
        transition: all 0.3s;
        &.facebook {
            background: #3a559f;
        }
        &.google {
            background: #d52d0b;
        }
        &.twitter {
            background: #3a559f;
        }
        &.linkedin {
            background: #3a91db;
        }
    }
}