.header {
    .navbar {
        padding: 0 !important;
        background-color: #fff;
    }
    &__logo {
        margin-right: 50px;
        margin-left: 20px;
    }
    .nav-item:hover .nav-link{
        color: $orange!important;
    }
    .active .nav-link {
        font-weight: 600;
    }
    .nav-link {
        color: #555 !important;
        font-size: 14px;
        text-transform: capitalize;
        padding: 10px !important;
    }
    .nav-button {
        background: #fff;
    }
    &-employer {
        background-color: #00648f;
        &:hover {
            background-color: #0473a2;
        }
        &__title {
            color: #fff;
            font-weight: 500;
            text-transform: uppercase;
        }
        &__desc {
            color: #00b9f2;
            font-size: 13px;
        }
    }
    &__bell {
        font-size: 19px;
    }
    .dropdown-toggle::after {
        display: none;
    }
    &__nagination {
        position: absolute;
        right: 390px;
    }
}

.fixed {
    position: fixed;
    top: 0;
    background: #fff;
    width: 100%;
    z-index: 9999;
    box-shadow: 0 0 3px rgba(0, 0, 0, 0.2);
    -webkit-animation-duration: .5s;
    animation-duration: .5s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-name: fadeInDown;
    animation-name: fadeInDown;
}

.notification {
    width: 375px;
    left: -172.5px;
    top: 50px;
    padding: 0;
    margin: 0;
    border-radius: 5px 5px 0 0;
    &::before {
        content: '';
        display: block;
        position: absolute;
        top: -0.375rem;
        left: 50%;
        width: 0.625rem;
        height: 0.625rem;
        margin-left: -0.21875rem;
        border-top: 1px solid #ebebeb;
        border-left: 1px solid #ebebeb;
        background-color: #fff;
        transform: rotate(45deg);
    }
    &__title {
        color: #555;
        margin: 0;
        text-transform: uppercase;
        padding: 12px 15px;
        font-size: 14px;
        font-weight: 700;
        line-height: 18px;
        border-bottom: 1px solid #ebebeb;
    }
    &__list {
        overflow: scroll;
        max-height: 270px;
    }
    &__content {
        padding: 12px 15px;
        border-bottom: 1px solid #ebebeb;
        &:last-child {
            border-bottom: none;
        }
        &:hover {
            background-color: #f7f7f7;
        }
    }
    &__img {
        img {
            height: 65px;
            width: 65px;
        }
    }
    &__desc {
        font-size: 14px;
        padding-left: 10px;
        line-height: 24px;
        letter-spacing: .2px;
    }
    &__btn {
        border-top: 1px solid #ebebeb;
        padding: 12px 15px;
        a {
            color: $orange;
            font-weight: 600;
        }
    }
}

.login-modal {
    &__content {
        padding: 15px 80px 25px;
        color: #555;
        border-radius: 0;
    }
    &__title {
        font-size: 32px;
    }
    &__btn {
        width: 100%;
        padding: 9px 7px;
        font-size: 14px;
        border: 1px solid #ccc;
        background: #fff;
        &:hover {
            background: #e0e0e0;
        }
        &__span {
            padding-left: 5px;;
        }
    }
    &__icon {
        height: 25px;
        width: 25px;
        display: inline-block;
    }
    &__user-email {
        text-align: center;
        font-size: 16px;
        margin-bottom: 30px;
        font-weight: 500;
        text-transform: uppercase;
    }
    &__forgot-password {
        margin-bottom: 30px;
        font-size: 14px;
        a {
            color: #00b9f2;
            &:hover {
                color: $orange;
            }
        }

    }
    .input-control {
        height: 42px;
        background: #fff;
        border: 1px solid #ddd;
        margin-bottom: 20px;
    }
    .input-register {
        margin-bottom: 0;
    }
    .checkbox-rigister {
        font-size: 14px;
        margin: 10px 0 30px;
        input {
            height: 16px;
            width: 16px;
            margin-right: 5px;
        }
    }
    .disclaimer-register {
        padding-top: 20px;
        font-size: 12px;
        color: #999;
    }
    .btn-red {
        width: 100%;
        padding: 8px;
        border-radius: 3px;
    }
    .modal-header {
        border: none;
    }
    &__light {
        margin-top: 30px;
        margin-bottom: 20px;
        border: 0;
        border-top: 1px solid #ddd;
    }
    &__register {
        font-size: 14px;
        a {
            font-weight: 700;
            color: #00b9f2;
            &:hover {
                color: $orange;
            }
        }
    }
    &__close {
            position: absolute;
    top: 10px;
    font-weight: 100;
    right: 20px;
    font-size: 34px;
    text-indent: 0;
    z-index: 1;
    }
    &__row {
        margin-bottom: 30px;
    }
    .row {
        margin-left: -5px;
        margin-right: -5px;
        .col-6, .col-lg-6 {
            padding-left: 5px;
            padding-right: 5px;
        }
    }
}

@media (min-width: 992px) {
    .modal-dialog {
        max-width: 660px;
        margin-top: 30px;
        margin-bottom: 30px;
    }
}
@media (max-width: 991px) {
    .header {
        .navbar {
            padding: 10px !important;
        }
        &__nagination {
            top: 10px;
            right: 80px;
        }
        &-employer {
            &__w100 {
                width: 100%;
                text-align: center;
            }
        }
    }
    .notification {
        left: inherit;
        right: -60px;
        top: 49px;
    }
}

@media (max-width: 400px) {
    .notification  {
        width: 320px;
        right: -80px;
    }
}
//
@-webkit-keyframes fadeInDown {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}
@keyframes fadeInDown {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, -100%, 0);
        transform: translate3d(0, -100%, 0);
    }
    100% {
        opacity: 1;
        -webkit-transform: none;
        transform: none;
    }
}
