$tsd-1:0 2px 2px rgba(0, 0, 0, 0.50);

/* Define mixin */

@mixin excerpt($lines-to-show) {
    display: -webkit-box;
    -webkit-line-clamp: $lines-to-show;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}