.footer {
    background-color: #313131;
    width: 100%;
    bottom: 0;
    padding-top: sz(43);
    &__end {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    &__license {
        font-size: sz(14);
        line-height: sz(22);
        text-align: center;
        color: #999;
    }
    &__social {
        display: flex;
        justify-content: center;
        margin-top: 10px;
        &-blade {
            color: #fff;
            text-align: center;
            width: 32px;
            line-height: 32px;
            border-radius: 0px;
            background: #444;
            display: inline-block;
            height: 32px;
            position: relative;
            overflow: hidden;
            margin-left: 5px;
            &:hover {
                color: #fff;
                background:$orange;
            }
        }
    }
    .copyright {
        border-top: 1px solid #353535;
        background: #1d2023;
        padding: 20px 0;
    }
}

.footer-menu {
    margin-bottom: 40px;
    &__blade {
        margin-bottom: sz(14);
        img {
            width: 100%;
        }
    }
    &__title {
        color: #fff;
        padding-bottom: 20px;
        font-weight: 700;
        letter-spacing: 1px;
        font-size: 24px;
    }
    &__link {
        display: flex;
        margin-bottom: sz(12);
        &:hover {
            .footer-menu__text {
                color: #fff;
            }
            .footer-menu__dot i {
                background-color: #fff;
            }
            i {
                color: #fff;
            }
        }
    }
    &__dot {
        display: flex;
        align-items: center;
        i {
            display: block;
            width: sz(7);
            height: sz(7);
            margin-right: sz(8);
            margin-bottom: sz(1);
            border-radius: 50%;
            background-color: #999;
        }
    }
    &__icon {
        color: #999;
        margin-right: 10px;
    }
    &__text {
        display: inline-block;
        max-width: 100%;
        overflow: hidden;
        text-overflow: ellipsis;
        font-size: sz(14);
        line-height: sz(22);
        white-space: nowrap;
        color: #999;
        &--address {
            white-space: normal;
        }
    }
}

@media (min-width: sz(544)) {
    .footer {
        &__license {
            text-align: left;
        }
    }
}

@media (min-width: sz(768)) {
    .footer {
        &__end {
            flex-direction: row;
        }
    }
}