.paginations {
    background: #fff;
    padding: 24px;
    &__nav {
        display: flex;
        flex-flow: row wrap;
        width: 100%;
        margin: 0 sz(-7);
    }
    &__link {
        display: flex;
        justify-content: center;
        align-items: center;
        min-width: sz(40);
        height: sz(40);
        margin: 0 sz(7) sz(7);
        padding: 0 sz(10);
        border: sz(1) solid #ddd;
        border-radius: sz(2);
        font-family: Roboto-Bold;
        font-size: sz(16);
        color: $color-grey-2;
        background: #fff;
        i {
            font-size: sz(12);
        }
        &:hover {
            color: #00b9f2;
            i::before {
                color: #00b9f2;
            }
        }
        &.active {
            box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12);
            color: #fff;
            background: #00b9f2;
        }
    }
}

@media (min-width: sz(992)) {
    .paginations {
        &__nav {
            justify-content: center;
        }
        &__link {
        	min-width: sz(45);
        	height: sz(45);
        }
    }
}