.comment {
	&__list {
		margin-bottom: 35px;
		li ul {
			padding-left: 85px;
		}
	}
	&__head {
		font-size: 20px;
	}
	&__avata {
		img {
			width: 75px;
			height: 75px;
		}
	}
	&__media {
		padding: 30px 0;
		border-bottom: 1px solid #ddd;
	}
	&__info {
		padding-left: 15px;
	}
	&__name {
		font-size: 16px;
		line-height: 25px;
	}
	&__date {
		font-size: 13px;
	}
	&__link {
		background: #CCCCCC;
	    font-size: 13px;
	    color: #fff;
	    text-transform: uppercase;
	    padding: 0px 14px;
	    	&:hover {
	    		background:$orange;
	    		color: #fff;
	    	}
	}
	textarea {
		height: auto;
	}
}
.comment-respond {
	&__head {
		padding: 0 0 35px;
	}
}

@media (max-width: 991px) {
	.comment-respond {
		margin-bottom: 40px;
	}
}