/* Set style default to elements */

body {
    font-family: Roboto-Regular;
}
a {
    color: #555;
}
a:hover {
    text-decoration: none;
    color: #555;
}

a:link {
    transition: all 0.3s;
}

button {
    border: none;
    box-sizing: border-box;
    box-shadow: none;
    cursor: pointer;
    transition: all 0.3s ease;
    &:focus {
        outline: none;
    }
}

img {
    width: 100%;
}


ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

input,
textarea {
    outline: none;
    color: $color-grey-2;
    &::-webkit-input-placeholder {
        /* Chrome/Opera/Safari */
        color: $color-light-grey-2;
    }
    &::-moz-placeholder {
        /* Firefox 19+ */
        color: $color-light-grey-2;
    }
    &:-ms-input-placeholder {
        /* IE 10+ */
        color: $color-light-grey-2;
    }
}

::-webkit-scrollbar {
    width: sz(4);
    background: transparent;
}

::-webkit-scrollbar-thumb {
    border-radius: sz(16);
    background: $orange;
}