.page-header {
    &__bg {
	    background: rgba(0,0,0,0.7);
	    padding: 60px 0;
    }
    &__wapper {
    	text-align: center;
    	color: #fff;
    	a {
    		color: #fff;
    		&:hover {
    			color: #00b9f2;
    		}
    	}
    	span {
    		padding:0 5px;
    	}
    }
    &__title {
    	font-size: 30px;
	    line-height: 38px;
	    font-weight: 700;
	    color: #fff;
	    text-transform: uppercase;
	    text-align: center;
    }
    &__current {
    	color: #00b9f2;
         padding: 0;
    }
}

.blog {
    background: #fff;
    padding: 80px 0;
    &__img {
        img {
            width: 100%;
        }
    }
    &__item {
        margin-bottom: 40px;
        padding-bottom: 40px;
        border-bottom: 2px solid #EFEFEF;
    }
    &__content {
        padding: 25px 0;
        text-align: center;
    }
    &__author {
        a {
            color: $orange;
            font-size: 13px;
            text-transform: uppercase;
        }
    }
    &__title {
        font-size: 22px;
        padding-bottom: 20px;
        border-bottom: 1px solid #EFEFEF;
        letter-spacing: 1px;
        a {
            color: #212121;
            &:hover {
                color: $orange;
            }
        }
    }
    .meta {
        margin-top: 10px;
    }
    &__desc {
        max-height: 100px;
        overflow: hidden;
    }
}

.mb20 {
    margin-bottom: 20px;
}