.jobs-detail-banner {
    height: 160px;
    width: 100%;
    background-position: top center;
    background-size: cover;
    &__overlay {
    	background-image: -moz-linear-gradient(-90deg,rgba(0,0,0,.2) 0,rgba(12,12,12,.5) 21%,rgba(23,23,23,.7) 48%,#171717 100%);
	    background-image: -webkit-linear-gradient(-90deg,rgba(0,0,0,.2) 0,rgba(12,12,12,.5) 21%,rgba(23,23,23,.7) 48%,#171717 100%);
	    background-image: -ms-linear-gradient(-90deg,rgba(0,0,0,.2) 0,rgba(12,12,12,.5) 21%,rgba(23,23,23,.7) 48%,#171717 100%);
	    height: 100%;
	    width: 100%;
	    opacity: .6;
    }
}

.jobs-detail {
	margin-top: -140px;
	&__share {
		margin-bottom: 20px;
		a {
			background: rgba(0,0,0,.1);
			color: #fff;
		    display: inline-block;
		    cursor: pointer;
		    opacity: .9;
		    padding: 10px 15px 8px;
		    border-radius: 3px;
		    transition: opacity .3s,background .3s;
		}
		i {
			padding-left: 5px;
		}
	}
}

.jobs-detail-header {
	padding: 25px 40px;
	background: #fff;
    margin-bottom: 15px;
    border: 0;
    box-shadow: 0 0.5px 0 rgba(0,0,0,.1);
    &__img {
    	height: 94px;
	    padding-top: 5px;
	    display: flex;
	    padding-right: 30px;
    	img {
    		max-height: 100%;
		    margin: auto;
		    max-width: 100%;
		    width: initial;
    	}
    }
    &__title {
    	font-weight: 700;
    	font-size: 25px;
    	margin-bottom: 8px;
    	color: #585858;
    	font-family: Roboto,Arial,sans-serif;
    }
    &__name {
    	text-transform: uppercase;
    	font-size: 14px;
    	color: #555;
    }
    &__light {
    	padding: 0 10px;
    }
    &__add {
    	font-size: 14px;
    	color: #555;
    }
    &__money {
    	color: $orange;
    	font-size: 16px;

    }
    &__view {
    	color: #999;
    	font-size: 14px;
    }
    .btn-red {
    	width: 100%;
    	margin-bottom: 5px;
    	margin-top: 20px;
	    margin-top: 4px;
	    font-size: 18px;
	    padding: 7px 20px;
	    border-radius: 3px;
    }
}

.jobs-detail-content {
	color: #555;
	margin-bottom: 60px;
	.nav-link {
		background: 0 0;
	    border: none;
	    border-bottom: 2px solid #eee;
	    padding: 12px 25px 10px;
	    text-transform: uppercase;
	    font-size: 14px;
		color: #424242;
    	&.active {
    		font-weight: 700;
		    font-size: 15px;
		    border-bottom: 2px solid #f7941d;
		    background: inherit!important;
		    border-color: inherit!important;
    	}
	}
	&__tab {
		padding: 40px;
		background: #fff;
		font-size: 15px;
	}
	&__title {
		font-size: 21px;
	    font-weight: 500;
	    text-transform: uppercase;
	    font-family: Roboto,Arial,sans-serif;
	    margin-top: 5px;
	    margin-bottom: 17px;
	}
	.benefits {
		margin-bottom: 45px;
	}
	.benefit {
		margin-bottom: 7px;
		&-icon {
			text-align: center;
			padding-right: 5px;
			i {
				width: 28px;
			    height: 28px;
			    border: 1px solid #666;
			    border-radius: 100%;
			    line-height: 28px;
			    font-size: 13px;
			}
		}
	}
	&__sibar {
		border: 1px solid rgba(0,185,242,.5);
	    padding: 23px;
	    background-color: #f7fdff;
	    border-radius: 3px;
	    color: #555;
	    .summary-item {
	    	padding: 12px 0;
    		border-bottom: 1px solid rgba(206,211,213,.5);
    		&:last-child {
    			border: none;
    		}
	    }
	    .summary-icon {
	    	font-size: 30px;
	    	padding-right: 10px;;
	    }
	    .summary-content {
	    	&__text {
	    		text-transform: uppercase;
			    color: #949697;
			    display: block;
			    font-weight: 300;
			    font-size: 13px;
	    	}
	    	&__date {
	    		font-size: 14px;
	    	}
	    }
	    .icon {
	    	vertical-align: middle;
		    display: inline-block;
		    background: center center no-repeat;
		    background-size: 100%;
	    }
	    .icon1 {
	    	background-image: url(../../images/icon1.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon2 {
	    	background-image: url(../../images/icon2.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon3 {
	    	background-image: url(../../images/icon3.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon4 {
	    	background-image: url(../../images/icon4.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon5 {
	    	background-image: url(../../images/icon5.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon6 {
	    	background-image: url(../../images/icon6.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon7 {
	    	background-image: url(../../images/icon7.png);
		    width: 25px;
		    height: 25.5px;
	    }
	    .icon8 {
	    	background-image: url(../../images/icon8.png);
		    width: 25px;
		    height: 25.5px;
	    }
	}
	&__slide {
		margin: 0 auto 20px;
	    position: relative;
	    border: 1px solid #ededed;
	    background: #f2f2f2;
	    padding: 0 0 30px;
	    .carousel-indicators {
	    	bottom: -11px;
	    }
	    .row {
	    	margin-left: -2px;
	    	margin-right: -2px;
	    }
	    .col-4 {
	    	padding-left: 2px;
	    	padding-right: 2px;
	    }
	}
	&__item {
		border-bottom: 1px solid #eef1f2;
    	padding: 13px 0;
    	&:last-child {
    		border: none;
    	}
    	.jobs-title {
    		font-size: 16px;
		    font-weight: 600;
		    color: #555;
		    &:hover {
		    	color: $orange;
		    }
    	}
    	.jobs-info {
    		font-size: 13px;
    	}
    	.jobs-money {
    		color: $orange;
    		font-weight: 400;
    	}
    	.jobs-light {
		    margin: 0 10px;
		    color: #ccc;
    	}
	}
	&__btn {
		font-size: 14px;
		margin-top: 15px;
		color: #00b9f2;
		&:hover {
			color: $orange;
		}
	}
}

.jobs-detail-favorite {
	&__box {
		margin-bottom: 30px;
	}
	&__head {
		text-transform: uppercase;
	    font-size: 21px;
	    font-weight: 500;
	}
	&__item {
	    background: #fff;
	    padding: 10px;
	    margin-bottom: 12px;
	    border: 0;
	    box-shadow: 0 0.5px 0 rgba(0,0,0,.1);
	}
	&__img {
		display: flex;
	    justify-content: center;
	    align-items: center;
	    height: 100%;
	    width: 25%;
	    padding-right: 10px;
    	max-height: 50px;
	    img {
	    	display: block;
		    max-width: 100px;
		    height: auto;
	    }
	}
	&__info {
		width: 75%;
	}
	&__title {
		font-weight: 500;
    	font-size: 16px;
	    white-space: nowrap;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    vertical-align: middle;
	    &:hover {
	    	color: $orange;
	    }
	}
	&__company {
		font-weight: 100;
	    font-size: 13px;
	    color: #999;
	    margin-top: -5px;
	}
	.row {
		margin-right: -5px;
		margin-left: -5px;
	}
	.col-lg-4, .col-lg-8, .col-lg-6 {
		padding-left: 5px;
		padding-right: 5px;
	}
}

.jobs-detail-course {
	margin-bottom: 40px;
	&__box {
	    padding: 24px 30px;
	    margin-top: 33px;
	    background: #fff;
	}
	&__img {
		max-width: 300px;
	    max-height: 100px;
	    width: inherit;
	}
	&__title {
		font-size: 20px;
		font-weight: 500;
		margin: 10px 0;
	}
	&__info {
		font-size: 14px;
	}
	.btn-red {
		margin-top: 20px;
	    background: #5cb85c;
	    border: 1px solid #5cb85c;
	    border-radius: 3px;
	    padding: 8px 15px;
	    &:hover {
	    	color: #fff;
	    	background: #3b923b;
	    }
	}
}

@media (min-width: 992px) {
	.jobs-detail-header {
		.row {
			margin-right: -5px;
			margin-left: -5px;
		}
		.col-lg-2, .col-lg-8 {
			padding-left: 5px;
			padding-right: 5px;
		}
	}
}

@media (max-width: 991px) {
	.jobs-detail {
		&__share {
			a {
				padding: 12px 12px 12px 7px;
			}
		}
	}
	.jobs-detail-content__box {
		margin-bottom: 40px;
	}
	.jobs-detail-favorite__box {
		margin-bottom: 0;
	}
	.jobs-detail-header {
		&__title {
			font-size: 22px;
		}
	}
}