.wrapper {
    background: #eee;
}

.btn-red {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 2px solid $orange;
    color: #fff;
    background: $orange;
    transition: all 0.3s;
    &:hover {
        color: $orange;
        background: #fff;
    }
}

.btn-red-back {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: 1px solid $orange;
    color: $orange;
    background: #fff;
    transition: all 0.3s;
    &:hover {
        color: #fff;
        background: $orange;
    }
}

.btn-common {
    border: 1px solid $orange;
    background-color: $orange;
    color: #fff;
    padding: 10px 30px;
    text-transform: uppercase;
    &:hover {
        color: $orange;
        background-color: #fff;
    }
}

.zoom-outer {
    overflow: hidden;
    &:hover {
        .zoom-inner {
            transform: scale(1.25)
        }
    }
}

.zoom-inner {
    transition: 0.5s;
}

.section-title {
    text-transform: capitalize;
    font-size: 36px;
    margin-bottom: 20px;
    margin-top: 20px;
    text-align: center;
    font-weight: 400;
}

.meta {
    margin-bottom: 10px;
}
.meta-part {
    margin-right: 25px;
    color: #999;
    &:hover {
        color: $orange;
    }
    i {
        padding-right: 8px;
        color: $orange;
    }
}

.input-control, textarea {
    width: 100%;
    height: 48px;
    font-size: 14px;
    font-style: italic;
    background: #F7F7F7;
    border: none;
    font-size: 15px;
    color: #999;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
    margin-bottom: 30px;
    padding: 10px 12px;
    border-radius: 0;
    border-bottom: 1px solid transparent;
    -webkit-transition: all 0.2s linear;
    -moz-transition: all 0.2s linear;
    -o-transition: all 0.2s linear;
    transition: all 0.2s linear;
    &:focus {
        border-bottom: 1px solid $orange;
        background: #F7F7F7;
    }
}

blockquote {
    padding: 35px 0;
    position: relative;
    margin: 10px 0 35px;
    background: #f5f5f5;
    border-left: 5px solid $orange;
        .quote-text {
        color: #2d2d2d;
        font-size: 18px;
        line-height: 32px;
        display: block;
        font-style: italic;
        font-weight: 400;
        padding-left: 45px;
    }
}

@media (min-width: 992px) {
    .w-lg-75 {
        width: 75%;
    }
}

@media (max-width: 991px) {
    .section-title {
        text-transform: uppercase;
        font-size: 21px;
        margin-bottom: 15px;
        margin-top: 15px;
        padding: 0 20px;
    }
}