.jobs-search {
	margin-top: 20px;
	&__item {
		background: #00b9f2;
		padding: 12px 12px 2px;
		border-radius: 3px;
		input {
			width: 100%;
		}
	}
	&__filter {
		padding: 10px;
		.ui.floating.dropdown {
			.text {
				font-weight: 600;
				input {
					display: none;
				}
			}
			input.search {
				z-index: 4;
			}
		}
		&__radio {
			width: 15px;
			height: 15px;
			margin-right: 7px;;
		}
	}
	&__text {
		width: 100px;
		color: #999;
		font-weight: 600;
	}
	&__flex {
		width: 100%;
	}
}

.jobs-nav {
	background: #fff;
	padding: 14px 20px;
    margin-bottom: 10px;
    height: 65px;
    &__title {
    	font-weight: 700;
    	text-transform: uppercase;
    	font-size: 14px;
    }
    &__link {
    	font-weight: 500;
    	color: #555;
    	.fa-heart {
    		color: $orange;
    		padding-right: 5px;
    	}
    	.fa-envelope {
    		color: #18bcf4;
    		padding-right: 5px;
    	}
    }
}
div.jobs-nav__link {
	padding-left: 20px;
}

.jobs {
	&__list {
		border: 1px solid #eee;
	}
	&__item {
		width: 100%;
		background: #fff;
		border-bottom: 1px solid #eee;
	}
	&__img {
		img {
			max-width: 75px;
			max-height: 45px;
		}
		min-width: 120px;
		border-right: 1px solid #eee;
	}
	&__content {
		padding: 20px;
		background: #eaf8fc;
		width: 100%;
		span {
			color: #999;
		}
	}
	&__title {
		a {
			font-size: 18px;
		    color: #555;
		    cursor: pointer;
		    font-weight: 600;
		    &:hover {
		    	color: $orange;
		    }
		}
		span {
			font-size: 11px;
			color: #ff1919;
		    font-weight: 400;
		    font-style: italic;
		    padding-left: 3px;
		}
	}
	&__company {
		color: #555;
	}
	&__meta {
		font-size: 14px;
		line-height: 21px;
	}
	&__separator {
		margin: 0 10px;
    	font-weight: 100;
    	color: #ccc;
	}
	&__money {
		color: $orange!important;
		font-weight: 700;
		padding-left: 5px;
	}
	&__add {
		font-weight: 700;
		padding-left: 5px;
	}
	&__saved {
		padding: 20px 20px 20px 0;
		background: #eaf8fc;
		i {
			padding-right: 3px;
		}
		&.active, &:hover {
			i {
				color: $orange;
			}
		}
	}
}

.jobs-blockquote {
	border-left: 5px solid #5cb85c;
	padding: 40px;
	background: #fff;
	margin-bottom: 60p;
	&__title  {
		font-size: 20px;
		font-weight: 700;
	}
	&__desc {
		margin-bottom: 10px;
		font-weight: 700;
	}
	.btn-red {
		margin-top: 20px;
		background: #5cb85c;
		border: 1px solid #5cb85c;
		border-radius: 3px;
		padding: 8px 15px;
		&:hover {
			background: #3b923b;
			color: #fff;
		}
	}
	img {
		width: 130px;
		float: right;
	}
}

.jobs-sibar {
	margin-bottom: 10px;
	padding: 16px;
	background: #fff;
    border: 0;
    box-shadow: 0 0.5px 0 rgba(0,0,0,.1);
	&__title {
		font-size: 14px;
	    font-weight: 700;
	    text-transform: uppercase;
	    border-bottom: 1px rgba(221,221,221,.66) solid;
	    margin: -2px -16px 16px -16px;
	    padding-left: 16px;
	    padding-bottom: 15px;
	}
	&__desc {
		font-size: 14px;
	}
	&__btn {
		padding: 8px;
		border-radius: 3px;
	    font-weight: 700;
	    font-size: 15px;
	}
}

@media (min-width: 992px) {
	.jobs-search {
		.row {
			margin-left: -2px;
			margin-right: -2px;
		}
		.d-flex {
			padding-left: 2px;
			padding-right: 2px;
		}
	}
	.jobs {
		.row {
			margin-left: -5px;
			margin-right: -5px;
		}
		.col-lg-9, .col-lg-3 {
			padding-left: 5px;
			padding-right: 5px;
		}
	}
}
@media (max-width: 991px) {
	.jobs-search {
		&__filter {
			.ui.floating.dropdown {
				display: block;
			}
		}
	}
}

@media (max-width: 767px) {
	.jobs {
		&__img {
			min-width: 90px;
		}
		&__content {
			padding: 10px;
		}
		&__saved {
			padding: 10px 10px 10px 0;
		}
		&__title {
			a {
				font-size: 15px;
			}
		}
		&__company {
			font-size: 14px;
		}
	}
}
