.slider-home {&__wapper {
		display: flex;
		justify-content: center;
		align-items: center;
		background: rgba(0,0,0,.1);
		height: 100%;
    	width: 100%;
	}
	&__img {
		width: 100%;
	}
	&__imgs {
		width: 130px;
		height: 60px;
	}
	&__bg {
		display: flex;
		background: rgba(0,0,0,.65);
		border-radius: 2px;
		padding: 10px;
	}
	&__link {
	    display: flex;
	    justify-content: center;
	    position: absolute;
	    bottom: 10px;
	    left: 0;
	    right: 0;
	}
	&__content {
		padding-left: 10px;
		padding-right: 10px;
		width: 250px;
	}
	&__title {
		color: #fff;
		font-weight: 700;
		padding-top: 7px;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	&__desc {
		color: #fff;
		overflow: hidden;
		text-overflow: ellipsis;
		white-space: nowrap;
	}
	&__icon {
		color: #fff;
		padding-left: 10px;
		border-left: 1px solid #fff;
	}
	.carousel-indicators {
		bottom: 100px;
	}
}

.search-widget {
	background: rgba(0,0,0,.65);
	border-radius: 2px;
	padding: 15px 20px;
	input {
		width: 100%;
	}
	&__title {
		font-size: 22px;
	    color: #fff;
	    margin-bottom: 15px;
	}
	&__input {
		position: relative;
		width: 100%;
		margin-bottom: 10px;
		input {
			width: 100%;
			padding: 10px 15px 10px 40px;
			border: 1px solid #868e96;
			border-radius: 3px;
		}
		i {
			position: absolute;
			left: 15px;
			top: 13px;
			color: #555;
		}
	}
	&__dropdown {
		width: 100%;
		padding: 10px 15px;
		border: 1px solid #868e96;
		border-radius: 3px;
		background: #fff;
		margin-bottom: 10px;
		i.fa-search {
			position: absolute;
			right: 33px;
			top: 30px;
			color: #555;
		}
	}
	.menu-scroll {
		max-height: 150px;
    	overflow: scroll;
	}
	&__icon {
		padding-right: 10px;
	}
	&__right {
		float: right;
	}
	&__btn {
		width: 100%;
		border-radius: 3px;
		text-transform: uppercase;
		padding: 8px;
		margin-bottom: 10px;
	}
	&__link:hover {
		color: $orange;
	}
}

.employer-home {
	padding: 40px 0 0;
	.row {
		margin-left: -5px;
		margin-right: -5px;
	}
	&__col {
		padding-left: 5px;
		padding-right: 5px;
	}
	&__item {
		position: relative;
		height: 150px;
		padding: 15px;
		margin-bottom: 10px;
		background: #fff;
		box-shadow: 0 0.5px 0 rgba(0,0,0,.1);
		overflow: hidden;
		img {
			max-width: 100%;
		    display: block;
		    height: auto;
		    width: inherit;
		}
	}
	&__tag {
		width: 115px;
	    height: 23px;
	    background: #f7941d;
	    position: absolute;
	    top: 13px;
	    text-align: center;
	    line-height: 22px;
	    color: #f0f0f0;
	    transform: rotate(45deg);
	    font-size: 12px;
	    right: -34px;
	    box-shadow: 0 0 2px rgba(171,60,0,.5);
	    font-weight: 700;
	}
}

.company-home {
	padding: 40px 0;
	&__banner {
		height: 300px;
		background-size: cover;
	    background-repeat: no-repeat;
	    width: 100%;
	}
	&__item {
		background: #fff;
		padding: 25px 40px 30px;
		border: 1px solid rgba(230,230,230,.8);
    	box-shadow: 1px 1px 1px #f1f1f1;
    	margin-top: -90px;
	}
	&__img {
		img {
			width: 160px;
		}
	}
	&__content {
		padding-left: 15px;
	}
	&__title {
		font-weight: 400;
	    margin-bottom: 2px;
	    margin-top: 0;
	    transition: color .3s;
	    display: inline-block;
	    font-size: 25px;
	    a {
	    	color: #555;
	    	&:hover {
	    		color: $orange;
	    	}
	    }
	}
	&__meta {
		color: #00b9f2;
		font-size: 15px;
	}
	&__desc {
		font-size: 15px;
	}
	.btn-red-back {
		padding: 10px 20px;
    	font-size: 16px;
    	border-radius: 3px;
    	font-weight: 600;
    	min-width: 170px;
	}
	.carousel-control-prev {
		left: 40%;
	}
	.carousel-control-next {
		right: 40%;
	}
}

.other-employer-home {
	padding: 40px 0 0;
	.row {
		margin-left: -5px;
		margin-right: -5px;
	}
	&__col {
		padding-left: 5px;
		padding-right: 5px;
	}
	&__item {
		height: 90px;
		padding: 15px;
		margin-bottom: 10px;
		background: #fff;
		box-shadow: 0 0.5px 0 rgba(0,0,0,.1);
		overflow: hidden;
		img {
			max-width: 100%;
		    display: block;
		    height: auto;
		    width: inherit;
		}
	}
	&__btn {
		color: #00b9f2;
		&:hover {
			color: $orange;
		}
	}
}

.latest-news {
	padding: 40px 0;
	&__item {
		margin-bottom: 30px;
	    background: #F9F9F9;
	    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2);
	    webkit-transition: all 0.2s ease-in-out;
	    moz-transition: all 0.2s ease-in-out;
	    transition: all 0.2s ease-in-out;
	    &:hover {
	    	background: #fff;
    		box-shadow: 0 16px 38px -12px rgba(0, 0, 0, 0.56), 0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
	    }
	}
	&__content {
		padding: 15px 15px 35px 0px;
	}
	&__title {
		font-size: 20px;
    	padding: 10px 0;
    	a {
    		display: block;
    		max-height: 44px;
    		overflow: hidden;
    		color: #212121;
    		&:hover {
    			color: $orange;
    		}
    	}
	}
	&__meta {
		padding: 6px 0;
	}
	&__meta-part {
		margin-right: 5px;
		a {
			color: #999;
			i {
				margin-right: 3px;
				color: $orange;
			}
			&:hover {
				color: $orange;
			}
		}
	}
	&__desc {
		max-height: 50px;
		overflow: hidden;
		margin-bottom: 25px;
	}
	.btn-common {
		padding: 10px 16px;
	}
	img {
		width: 100%;
		height: 100%;
	}
}

@media (max-width: 991px) {
	.latest-news {
		&__content {
			padding: 15px 15px 35px 25px;
		}
	}
}

@media (min-width: 992px) {
	.search-widget {
		.row {
			margin-left: -2px;
			margin-right: -2px;
		}
		.d-flex {
			padding-left: 2px;
			padding-right: 2px;
		}
	}
	.company-home {
		padding: 80px 0 ;
		.row {
			justify-content: center;
		}
	}
}

@media (max-width: 991px) {
	.company-home {
		&__banner {
			height: 200px;
		}
		&__item {
			flex-direction: column;
			text-align: center;
			padding: 20px 30px 30px 30px;
		}
		&__content {
			padding: 0;
		}
		&__title {
			font-size: 18px;
		}
	}
	.other-employer-home {
		&__item {
			height: 55px;
		}
	}
}

@media (max-width: 767px) {
	.slider-home {
		.carousel-indicators {
			display: none;
		}
		&__link {
			display: none;
		}
	}
	.search-widget__title {
		text-align: center;
		strong {
			display: block;
		}
	}
}