.jobs-home {
	padding: 40px 0 0;
	&__box {
		border: 1px solid #f2f2f2;
	    background: #fff;
	    padding: 35px 40px 80px;
	}
	&__nav {
		border: none;
		margin-bottom: 40px;
		li {
			text-align: center;
		}
		.nav-link {
			background: 0 0;
		    border: none;
		    border-bottom: 2px solid #eee;
		    padding: 5px;
		    text-transform: uppercase;
		    &:hover {
		    	color: $orange;
		    }
		    &.active {
		    	border-bottom: 2px solid $orange;
		    	font-weight: 600;
		    }
		}
	}
	&__img {
		float: left;
		img {
			width: 87px;
		}
	}
	&__content {
		clear: both;
		padding: 15px 0;
		border-bottom: 1px solid #eaeaea;
	}
	&__info {
		padding-left: 97px;
		padding-top: 7px;
	}
	&__title {
		text-transform: capitalize;
		font-weight: 600;
	    color: #555;
	    transition: color .3s;
	    margin-bottom: 0;
	    font-size: 17px;
	    line-height: 20px;
	    display: block;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	    &:hover {
	    	color: $orange;
	    }
	}
	&__desc {
		font-weight: 400;
	    color: #666;
	    font-size: 17px;
	    overflow: hidden;
	    text-overflow: ellipsis;
	    white-space: nowrap;
	}
	&__span1 {
		text-transform: uppercase;
	}
	&__span2 {
		text-transform: capitalize;
	}
}
.carousel-indicators {
	bottom: -70px;
	height: 17px;
	li {
		border-radius: 0px;
        width: 13px;
        height: 13px;
        margin: 0 5px;
        background: #ddd;
        border: none;
        border-radius: 50%;
        box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
            0 3px 1px -2px rgba(0, 0, 0, 0.2),
            0 1px 5px 0 rgba(0, 0, 0, 0.12);
        transition: all 0.4s ease-in-out;
        -moz-transition: all 0.4s ease-in-out;
        -webkit-transition: all 0.4s ease-in-out;
        -o-transition: all 0.4s ease-in-out;
        &:hover, &.active {
            opacity: 1;
            width: 18px;
            height: 18px;
            background: #fff;
        }
	}
	li.active {
		background: $orange;
		height: 17px;
	    width: 17px;
	    opacity: 1;
	}
}
.carousel-control-prev,
.carousel-control-next {
	top: inherit;
	bottom: -60px;
	background: #d8d8d8;
	height: 30px;
	width: 30px;
	border-radius: 50%;
	z-index: 20;
	opacity: 1;
	&:hover {
		background: #929090;
	}
}
.carousel-control-prev {
	left: 30%;
}
.carousel-control-next {
	right: 30%;
}

@media (min-width: 768px) {
	.jobs-home {
		&__nav {
			li {
				width: 220px;
			}
		}
	}
}

@media (max-width: 767px) {
	.jobs-home {
		&__box {
			padding: 35px 10px 80px;
		}
	}
	.carousel-control-prev,
	.carousel-control-next {
		display: none;
	}
}