.blog-sibar {
	&__head {
		font-size: 15px;
	    font-weight: 700;
	    letter-spacing: 1px;
	    text-transform: uppercase;
	}
	&__content {
		margin-bottom: 25px;
	}
	&__img {
		img {
			width: 120px;
		}
	}
	&__info {
		padding-left: 10px;
	}
	&__date {
		font-size: 14px;
	}
	&__title {
		font-size: 16px;
		color: #555;
		max-height: 43px;
		overflow: hidden;
		display: block;
		&:hover {
			color: $orange;
		}
	}
	&__categories {
		margin-bottom: 22px;
		width: 100%;
	}
	&__list {
		border-bottom: 1px solid #EFEFEF;
    	padding: 12px 0;
    	i {
    		color: $orange;
    		font-size: 18px;
    		margin-right: 10px;
    	}
    	a {
    		color: #999;
    		&:hover {
    			color: $orange;
    		}
    	}
	}
	&__search {
		display: flex;
		.form-control {
			flex-grow: 1;
		}
		.btn-search {
			padding: 0px 18px;
		    height: 48px;
		    border: none;
		    color: #fff;
		    background: #2d2d2d;
		    font-size: 18px;
		    line-height: 36px;
		    text-align: center;
		    transition: all 0.2s ease-in-out;
		    -moz-transition: all 0.2s ease-in-out;
		    -webkit-transition: all 0.2s ease-in-out;
		    -o-transition: all 0.2s ease-in-out;
		}
	}
}
